/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 4.5vh;
    height: 2.5vh;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(71, 32, 0);
    transition: .4s;
    border-radius: 5.2vh;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 2vh;
    width: 2vh;
    left: 0.15vh;
    bottom: 0.1vh;
    background-color: rgb(252, 215, 5);
    transition: .4s;
    border-radius: 50%;
  }
  
  input:checked + .slider {
    background-color: #f37e11;
  }
  
  input:focus + .slider {
    box-shadow: 0vh 0vh 0.3vh #ffc400;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(2.2vh);
    transform: translateX(2.2vh);
  }
